import React from 'react'
import './Recomendedvideoscoard.css'
import { Avatar } from '@mui/material';
import {Link} from 'react-router-dom';

function Recomendedvideoscard(props) {
  const {item } = props;
  return (
    <div className='card'>
      <Link to={item.url}><img src={item.bestThumbnail.url} alt="" /></Link>
    
    <div className="cardbottom">
    <div className='cardfirst'>
        <Link to={item.author.url}>{<Avatar className="cardavatar" src={item.author.bestAvatar.url}/>}</Link>
    </div> 

    <div className="cardend">
      <div className="title">{item.title}</div>
      <Link to={item.author.url}><div className="chanel">{item.author.name}</div></Link>
      <div className="views time muted">{item.views} views| {item.uploadedAt}</div>
    </div>
    </div>
    </div>
  )
}

export default Recomendedvideoscard
